<template>
	<div id="svip-update-area">
		<public-header title="修改会员区域"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell title="当前会员区域">
					<span class="font-bold" slot="right-icon">{{svip_area.present.agentTitle}}</span>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="下月会员区域">
					<div class="flex" slot="right-icon" @click="onTogglePopup('agent')">
						<span class="font-bold">{{svip_area.next.agentTitle}}</span>
						<van-icon class="c-gray margin-5-l" name="arrow"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="tips">
				<div>区域修改说明：</div>
				<div>1.每月的会员专享红包限购买会员时选择的区域使用；</div>
				<div>2.你可以修改你下月的会员区域，下月领取的红包可在修改后的区域使用。</div>
			</div>
		</div>
		<van-popup v-model="popup.agent" position="bottom">
			<van-picker
					title="选择会员区域"
					show-toolbar
					:columns="agents"
					:default-index="2"
					@cancel="onTogglePopup('agent')"
					@confirm="onConfirmAgent"
					value-key="area"
			/>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon";
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				agents: [],
				svip_area: {
					present: {},
					next: {}
				},
				popup: {
					agent: false,
				},
				showPreLoading: false
			}
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: that,
					url: 'svip/mine/updateArea',
					data: {},
					success: (res) => {
						that.agents = [...res.agents];
						that.svip_area = that.util.extend(that.svip_area, res.svip_area);
					},
					fail: (res) => {
						that.util.$toast(res.message, that.util.getUrl({path: '/package/pages/svip/mine'}), 1000, 'replace')
					}
				})
			},

			onTogglePopup(key) {
				this.popup[key] = !this.popup[key];
			},

			onConfirmAgent(value, index) {
				var that = this;
				if(value.id == that)
				that.svip_area.next.agentid = value.id;
				that.svip_area.next.agentTitle = value.title;
				that.onTogglePopup('agent');
			}
		},
		mounted() {
			this.onLoad();
		},
	}
</script>

<style>
	#svip-update-area #public-header .van-nav-bar{
		background-color: #14110f!important;
	}
	#svip-update-area .tips{
		padding: 10px 15px 0;
		line-height: 1.6;
		font-size: 12px;
		color: #999;
	}
</style>


